/* Ensure the body and html take full height */
html, body {
  height: 100%;
  margin: 0;
}

/* Flex container that takes full viewport height */
.container {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center;     /* Center vertically */
  height: calc(100vh - 75px);           /* Full height of the viewport */
}

.current {
  width: 100%; /* Adjust the width as needed */
  max-width: 400px; /* You can limit the width to keep the form compact */
  padding: 20px;
  border-radius: 10px;
  background: #f5f5f5; /* Optional background to highlight the form */
  .mb-i:focus{
    border-color: #1B1212;
  }
}


